<template>
    <div class="JH_login">
      <h2>비밀번호 입력</h2>
      <input
        type="password"
        v-model="password"
        placeholder="비밀번호를 입력하세요"
      />
      <button @click="checkPassword">로그인</button>
      <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        password: '',
        errorMessage: ''
      };
    },
    methods: {
      checkPassword() {
        const correctPassword = '00000'; // 지정된 비밀번호
        if (this.password === correctPassword) {
          this.$emit('login-success');
        } else {
          this.errorMessage = '비밀번호가 틀렸습니다.';
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .JH_login {
    text-align: center;
  }
  .error {
    color: red;
  }
  </style>